<script>
import ModalMixin from "../../Mixins/Modal";
export default {
	props: {
		type: {
			type: String,
			default: "post",
			required: false,
		},
		postId: {
			type: Number,
			default: undefined,
			required: false,
		},
		commentId: {
			type: Number,
			default: undefined,
			required: false,
		},
	},
	mixins: [ModalMixin],

	methods: {
		setReason(val) {
			this.reason = val;
		},
		submit() {
			this.loading = true;
			let data = {
				report: this.reason,
			};
			if (this.type == "post") {
				data.post_id = this.postId;
			} else if (this.type == "comment") {
				data.comment_id = this.commentId;
			}
			axios
				.post("/reportpost", data)
				.then((response) => {
					if (response.data.result) {
						this.toast("     ", "check", "text-success");
						this.$emit("update:show", false);
					} else {
						this.handleError(response.data.errors);
					}
				})
				.catch((err) => this.toast("   "))
				.then(() => (this.loading = false));
		},
	},
	data() {
		return {
			reportFor: null,
			loading: false,
			description: null,
			moreInfo: false,
			reason: null,
		};
	},
};
</script>

